.checked {
    background-color: crimson;
}

/* Only works with 5x5 table. Need a manual fix in a changed num of table's rows case */

.table td {
    width: 10%;
    position: relative;
    vertical-align: middle;
}


@media(max-width: 600px) {
    .table td {
        padding: 2px;
        vertical-align: middle;
    }
    p {
        line-height: 1;
        font-size: 10px;
        margin: 6px 2px;
    }
}